import React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import { useStyles } from "./HeaderStyle";
import { Hidden, IconButton } from "@material-ui/core";
import MenuOpenIcon from "@mui/icons-material/MenuOpen";
import image from "../images/navlogo.jpg"
import { Link } from "react-scroll";

const Navbar = ({ navlinks, handleDrawerToogler }) => {
  const classes = useStyles();
  return (
    <Box>
      <AppBar position="fixed" className={classes.navbar}>
        <Toolbar className={classes.ToolBar}>
          <Typography style={{display: "flex"}} variant="h5" component="h6" sx={{ flexGrow: 1 }}>
          <img src={image} style={{width: "10%"}} alt="" />
          </Typography>
          <Box component={Hidden} xsDown>
            <Box>
              {navlinks.map((item, i) => (
                <Button key={i} className={classes.navlinks}
                activeClass= 'active'
                to={`${item.id}`}
                spy={true}
                smooth={true}
                offset={-70}
                duration={500}
                color='inherit'
                // component={Link}
                >
                  {item.label}
                </Button>
              ))}
            </Box>
          </Box>
          <Box component={Hidden} smUp>
            <IconButton
              color="inherit"
              onClick={handleDrawerToogler}
              style={{ padding: "0" }}
            >
              <MenuOpenIcon />
            </IconButton>
          </Box>
        </Toolbar>
      </AppBar>
    </Box>
  );
};
export default Navbar;
